import { useState, useEffect } from 'react';
import { getUserFollowers } from '../service/follower.service';

type Props = (
  userID: string,
  accessToken: string,
  jwtAccessToken: string,
) => {
  followers: string[];
  errorMsg?: string;
};

/**
 *  userID  accessToken  user followers <br />
 * @param userID 17 live  account userID
 * @param accessToken 17 live  account accessToken
 * @param jwtAccessToken 17 live  account JWT
 * @returns  followers  errMsg 
 */
export const useFollower: Props = (userID, accessToken, jwtAccessToken) => {
  const [followers, setFollowers] = useState<string[]>([]);
  const [errorMsg, setErrorMsg] = useState<string>('');

  useEffect(() => {
    const fetchFollowers = async () => {
      try {
        const callback = (data: string[]) => {
          setFollowers(data);
        };
        const data = await getUserFollowers({
          userID,
          accessToken,
          jwtAccessToken,
          callback,
        });
        setFollowers(data);
        setErrorMsg('');
      } catch (error) {
        setFollowers([]);
        if (error?.response && error?.response.data) {
          setErrorMsg(error?.response.data?.errorMessage ?? 'something wrong!');
        } else {
          setErrorMsg('something wrong!');
        }
      }
    };

    fetchFollowers();
  }, [userID, accessToken, jwtAccessToken]);

  return { followers, errorMsg };
};

export default useFollower;
